<template>
    <div class="information">
        <form action="/">
            <van-search
                v-model="value"
                show-action
                placeholder="请输入搜索关键词"
                @search="onSearch"
                @cancel="onCancel"
            />
        </form>
        <van-tabs v-model="active"  animated sticky >
            <van-tab title="全部">
                <List sort @click="handleClick"  />
            </van-tab>
            <van-tab title="分类 1">
                <List sort @click="handleClick"  num="2" />
            </van-tab>
            <van-tab title="分类 2">
                <List sort @click="handleClick"  num="3" />
            </van-tab>
            <van-tab title="分类 3">
                <List sort @click="handleClick"  num="4" />
            </van-tab>
            <van-tab title="分类 4">
                <List sort @click="handleClick"  num="2" />
            </van-tab>
            <van-tab title="分类 5">
                <List sort @click="handleClick"  num="3" />
            </van-tab>
        </van-tabs>
    </div>
</template>

<script>
import { Toast } from 'vant';
import List from "@/components/listUI"

export default {
    data() {
        return {
            list: [],
            loading: false,
            finished: false,
            value: '',
            active: 2,
        };
    },
    components: {
        List
    },
    methods: {
        handleClick(info) {
            this.$router.push({
                name: "details",
                params: {
                    id: info.id,
                },
                query: {
                    info,
                }
            })
        },
        onSearch(val) {

            Toast("功能暂未开放");
            this.value = ""
        },
        onCancel() {
        Toast('取消');
        },
        onLoad() {
            // 异步更新数据
            // setTimeout 仅做示例，真实场景中一般为 ajax 请求
            setTimeout(() => {
            for (let i = 0; i < 10; i++) {
                this.list.push(this.list.length + 1);
            }

            // 加载状态结束
            this.loadings = false;

            // 数据全部加载完成
            if (this.list.length >= 40) {
                this.finished = true;
            }
            }, 1000);
        },
    },
}
</script>

<style lang="scss" scoped>
    .information{
    background-color: rgb(232, 232, 232);

    }
</style>